.social {
  display: -ms-flexbox;
  display: flex;
  height: 25px;
  text-decoration: none;
  color: #fff; }
  .social:hover .social__icon path, .social:hover .social__icon--telegram path, .social:hover .social__icon--facebook path, .social:hover .social__icon--skype path, .social:hover .social__icon--linkedin path {
    fill: #e51c23; }
  .social .social__icon, .social .social__icon--telegram, .social .social__icon--facebook, .social .social__icon--skype, .social .social__icon--linkedin {
    height: 100%; }
    .social .social__icon path, .social .social__icon--telegram path, .social .social__icon--facebook path, .social .social__icon--skype path, .social .social__icon--linkedin path {
      fill: #999; }



